___CSS_LOADER_EXPORT___.push([module.id, `.report-container {
    margin: 0 auto;
    background-color: #333;
    color: white;
    padding: 20px;
    border-radius: 10px;
}

h1 {
    text-align: center;
    margin-bottom: 20px;
}

.get-location-btn,
.submit-btn,
input[type="file"],
input[type="text"],
select {
    display: block;
    width: 100%;
    padding: 12px;
    margin: 10px 0;
    background-color: #0066cc;
    border: none;
    border-radius: 5px;
    color: white;
    cursor: pointer;
    text-align: center;
    box-sizing: border-box;
}

input[type="file"],
input[type="text"],
select {
    background-color: #444;
    color: white;
    cursor: auto;
}

.get-location-btn:hover,
.submit-btn:hover {
    background-color: #004c99;
}

.section {
    margin-bottom: 20px;
}
